import axios from "axios";

//è·å–è¯¥æ—¶é—´æ®µå†…çš„æ‰§è¡Œæƒ…å†µæ•°æ®
const getTodoListApi = (pageNum, pageSize) => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/todolist',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            pageNum: pageNum,
            pageSize: pageSize
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

