const getUserListApi = () => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/findAll',
        headers:{
            "Content-Type": "application/json",
        }
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//æäº¤é€‰æ‹©çš„è¢«è¯„ä¼°äºº
const getEvaluatedListApi = (userIds) => new Promise((resolve, reject) =>{
    let data = {
        userIds: userIds
    };
    axios({
        method:'post',
        url:'/api/add/evaluated',
        headers:{
            "Content-Type": "application/json",
        },
      data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

export{
    getTodoListApi,
    getUserListApi,
    getEvaluatedListApi
}